nav{
    background: rgba(23, 92, 196, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.9rem;
    border-radius: 50px;
    box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(20px);
}

nav a{
    background:transparent ;
    font-size: 1.1rem;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}
nav a.active{
    background: var(--color-bg);
    color: black;
}
